body {
  margin: 0 0 0 0;
  padding: 0px 0;

  font-family: sans-serif;
}

.center {
  text-align: center;
}

.header {
  margin: 22px 0;
}
#config {
  padding: 32px;
}
.resultsTable th {
  font-size: 32px;
  color: #333;
  text-transform: uppercase;
}

.drawer {
  padding: 16px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

#Filters {
  padding: 16px;
  color: white;
}

#results {
  padding: 32px;
  background: #ededed;
}

#results .item {
  padding: 32px;
  margin: 16px;
  background: #fff;
}

#results p em {
  font-size: 18px;
  display: inline-block;
  font-style: normal;
  padding: 0 16px;
  width: 120px;
  text-transform: capitalize;
}

.progress {
  width: 100%;
  margin: 120px 60px;
  text-align: center;
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}



